@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital@0;1&display=swap');

html, body {
	margin: 0;
	padding: 0;

	width: 100%;
	height: 100%;
}

body {
	color: #222222;
	background-color: #C0C0C0;
	font-size: 24px;
	font-family: 'Source Sans Pro', sans-serif;
}

/* --- LAYOUT --------------------------------------------------------------- */

#root {
	display: flex;
	flex-direction: column;
	align-items: stretch;

	width: 100%;
	height: 100%;
}

#top {
	display: flex;
	align-items: stretch;

	flex: 0 0 48px;

	padding: 4px;
}

#content {
	flex: 1;
}

#label {
	align-self: center;
	flex: 1;

	padding: 0 16px;
}

#menu {
	flex: 0 0 48px;
}

#menu > button {
	height: 100%;
	width: 100%;
}

#menu > .dropdown {
	position: absolute;
	top: 52px;
	right: 4px;
}

.dropdown {
	display: none;

	margin: 0;
	padding: 4px;

	font-size: 16px;
	list-style-type: none;
	user-select: none;

	box-sizing: border-box;
	border: none;
	color: transparent;
	text-shadow: 0 0 #222222;
	background: #C0C0C0;
	box-shadow:
		inset -1px -1px #0A0A0A,
		inset 1px 1px #FFFFFF,
		inset -2px -2px #808080,
		inset 2px 2px #DFDFDF;
	border-radius: 0;
}

.dropdown.show {
	display: initial;
}

.dropdown li:hover {
	color: #FFFFFF;
	background-color: #000080;
}

.dropdown li {
	padding: 4px;
}

#event-buttons {
	display: flex;
	align-items: stretch;

	flex: 0 0 128px;

	padding: 4px;
}

#event-buttons-serve {
	display: flex;
	flex-direction: column;
	align-items: stretch;
}

#names {
	display: flex;
	justify-content: space-between;
	align-items: stretch;

	padding: 4px;
}

#names > .name {
	text-align: center;
}

#scores {
	table-layout: fixed;

	padding: 4px;
}

#scores tr {
	display: flex;

	width: 100%;
}

#scores .name {
	text-align: left;
}

#scores .score {
	text-align: center;
}

#scores td {
	padding: 4px;
}

/* --- GENERIC ITEMS -------------------------------------------------------- */

.flex-32px {
	flex: 0 0 32px;
}

.flex-1 {
	flex: 1;
}

.flex-2 {
	flex: 2;
}

button {
	font-size: 14px;

	box-sizing: border-box;
	border: none;
	color: transparent;
	text-shadow: 0 0 #222222;
	background: #C0C0C0;
	box-shadow:
		inset -1px -1px #0A0A0A,
		inset 1px 1px #FFFFFF,
		inset -2px -2px #808080,
		inset 2px 2px #DFDFDF;
	border-radius: 0;
}

button:not(:disabled):active {
	box-shadow:
		inset -1px -1px #FFFFFF,
		inset 1px 1px #0A0A0A,
		inset -2px -2px #DFDFDF,
		inset 2px 2px #808080;
	text-shadow: 1px 1px #222222;
}

:disabled {
	color: #808080;
}

button:disabled {
	text-shadow: 1px 1px 0 #FFFFFF;
}

table {
	border-spacing: 0;
}

td, th {
	border-top: 1px solid black;
	border-left: 1px solid black;
}

td:last-child, th:last-child {
	border-right: 1px solid black;
}

tr:last-child > td, tr:last-child > th {
	border-bottom: 1px solid black;
}
